// Placeholder container
.box-placeholder {
  margin-bottom: @placeholder-margin-vertical;
  padding: @placeholder-padding;
  border: 1px dashed @placeholder-border;
  background: @placeholder-background;
  color: @placeholder-color;
}


// Remove margin from the last-child

.box-placeholder > :last-child { margin-bottom: 0; }

// Variant

.box-placeholder-lg {
  padding-top: @placeholder-large-padding-vertical;
  padding-bottom: @placeholder-large-padding-vertical;
}

/*
==============================================
expandUp
==============================================
*/


.expandUp{
  animation-name: expandUp;
  -webkit-animation-name: expandUp;

  animation-duration: 0.7s;
  -webkit-animation-duration: 0.7s;

  animation-timing-function: ease;
  -webkit-animation-timing-function: ease;

  visibility: visible !important;
}

@keyframes expandUp {
  0% {
    transform: translateY(100%) scale(0.6) scaleY(0.5);
  }
  60%{
    transform: translateY(-7%) scaleY(0.8);
  }
  75%{
    transform: translateY(3%);
  }
  100% {
    transform: translateY(0%) scale(1) scaleY(1);
  }
}

@-webkit-keyframes expandUp {
  0% {
    -webkit-transform: translateY(100%) scale(0.6) scaleY(0.5);
  }
  60%{
    -webkit-transform: translateY(-7%) scaleY(0.8);
  }
  75%{
    -webkit-transform: translateY(3%);
  }
  100% {
    -webkit-transform: translateY(0%) scale(1) scaleY(1);
  }
}
