.iconmelon {
  cursor: pointer;
  position: relative;
  width: 25px;
  height: 25px;
  display: inline-block;
  fill: @brand-primary;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }



  &.iconmelon-sm {
    width: 20px;
    height: 20px;
  }

  &.iconmelon-right {
    position: absolute;
    top: 8px;
    right: 8px;
  }

  &.iconmelon-left {
    position: absolute;
    bottom: 8px;
    left: 8px;
  }

  &.iconmelon-square {
    background-color: @brand-primary;
    fill: @brand-white;
    padding: 5px;
    position: absolute;
    bottom: -3px;
    right: 10px;
    width: 35px;
    height: 35px;
    .shadow-z1;
  }

  &:hover, &:active, &:focus {
    fill: @brand-primary-dark;

    &.iconmelon-square {
      background-color: @brand-primary-dark;
      fill: @brand-white;
    }

  }
}

