/* Menu styles */

.search-menu {
	position: relative;
	#searchInput {
		@media @upto-tablet {
			font-size: 12px;
			padding: 5px 10px;
			border-bottom: 1px solid #ececec!important;
		}
		display: block!important;
		background-color: transparent!important;
		border: none!important;
		font-size: 18px;
		padding: 30px;

	}

	.media {
		margin-top: 0;
	}
}

.search-menu-wrap {
	position: relative;
	overflow: hidden;
}

.search-menu-level {
	visibility: hidden;
	display: none;
	overflow: hidden;
	height: 100%;
	margin: 0;
	padding: 0;
	list-style-type: none;
}



.search-menu-level-current {

	cursor: default!important;
	visibility: visible;
	display: block;
	.form-control;
	height: auto;
	padding: 0 5px;

	ul {
		margin-bottom: 0;
	}
	.filter-item {
		position: relative;
		display: inline-flex;
		padding: 0 35px 0 5px;
		height: 35px;
		line-height: 35px;
		border-radius: 20px;
		width: auto;
		margin: 5px 2px;
		background-color: #e5e5e5;
		color: #303030;
		.filter-icon {
			display: inline;
			margin: 0 5px;
			line-height: 35px;
			font-size: 18px;
		}
		.filter-name {
			display: inline;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.close {
			position: absolute;
			height: 35px;
			line-height: 35px;
			right: 5px;
			font-size: 14px;
			opacity: 1;
			text-shadow: none!important;
			&:before{
				-moz-transition: all .2s ease-in;
				-o-transition: all .2s ease-in;
				-webkit-transition: all .2s ease-in;
				transition: all .2s ease-in;
				color: #9c9c9c;
				padding: 2px 4px 2.5px 4px;
				border-radius: 50%;
			}


		}
		&:hover .close:before {
			color: darken(#9c9c9c, 25);
		}
	}
	.filter-item-groups {
		.filter-icon{
			color: lighten(@brand-success, 5);
		}
	}
	.filter-item-interests {
		.filter-icon{
			color: lighten(@brand-primary, 5);
		}
	}
	.filter-item-location {
		.filter-icon{
			color: lighten(@brand-danger, 5);
		}
	}
	.filter-item-referrals {
		.filter-icon{
			color: lighten(@brand-warning, 5);
		}
	}

	@media @upto-tablet {
		ul {
			max-height: 250px;
			overflow-y: scroll;
		}

		.filter-item {
			padding: 0 25px 0 3px;
			height: 25px;
			line-height: 25px;
			border-radius: 20px;
			margin: 5px 2px;
			.filter-icon {
				margin: 0 5px;
				line-height: 25px;
				font-size: 12px;
			}
			.filter-name {
				font-size: 12px;
			}
			.close {
				height: 25px;
				line-height: 25px;
				right: 2px;
				font-size: 12px;
				&:before {

					padding: 2px 4px 2.5px 4px;
				}
			}
			&:hover .close:before {
				color: darken(#9c9c9c, 25);
			}
		}
	}
}

.search-menu-item {
	display: block;
	width: 100%;
	border-bottom: 1px solid rgba(218, 218, 218, 0.3);
}

label.search-menu-link, a.search-menu-link {
	font-weight: 600;
	position: relative;
	display: block;
	padding: 1em 0 0 0;
	color: @brand-success;
	-webkit-transition: color 0.1s;
	transition: color 0.1s;
	font-size: 18px;
	i {
		padding: 0.25em 1.25em;
		color: #fff;
	}
	.switch input + span {
		background-color: #fff;
		border-color: #fff;
		&:after {
			top:0;
		}
	}
	.switch input:checked + span {
		background-color: @brand-warning;
		border-color: @brand-warning;
		&:after {
			top:0;
		}
	}
}

a.search-menu-result {
	display: block;
	align-items: center;
	position: relative;
	vertical-align: middle;
	width: 100%;
	border-bottom: 1px solid #e6e6e6 !important;
	font-size: 12px;
	color: @brand-success;
	line-height: 50px;

	i.type-icon, img {
		color: @brand-success;
		width: auto;
		min-width: 50px;
		height: 50px;
		display: inline;
		line-height: 50px;
		float: left;
		text-align: center;
		vertical-align: middle;
		font-size: 18px;
		padding: 0;
		margin: 0 5px;
		&:before {
			width: 50px;
			display: inline-block;
		}
	}

	.search-item-name {
		display: inline-block;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		width: 75%;
	}
	.search-action-item {
		position: absolute;
		top: 5px;
		right: 5px;
		line-height: normal;
		padding: 2px 10px!important;
		border-radius: 10px;
		display: inline;
		color: white;
		i {
			color: white;
			padding: 0;
			width: auto;
			margin: 0 5px!important;
			font-size: 10px;

		}
		&.action-add {
			background-color: @brand-success;
		}
		&.action-remove {
			background-color: @brand-danger;
		}
		i {

		}
	}
	&.active {
		i {
			color: #a3a3a3;
		}
		color: #a3a3a3;
		.search-action-item i {
			color: white;
		}
	}



}




.search-menu-back {
	font-size: 12px;
	position: absolute;
	z-index: 100;
	top: 0;
	right: 2.25em;
	margin: 0;
	padding: 1.365em 0.65em 0 0;
	cursor: pointer;
	color: @brand-warning;
	border: none;
	background: none;
}

.search-menu-back-hidden {
	pointer-events: none;
	opacity: 0;
}

.search-menu-back:hover,
.search-menu-back:focus {
	color: @brand-success;
	outline: none;
}

.group-search, .interests-list {
	.filter-item {
		position: relative;
		display: inline-flex;
		padding: 0 35px 0 10px;
		height: 40px;
		line-height: 40px;
		border-radius: 20px;
		width: auto;
		margin: 5px 2px;
		background-color: #e5e5e5;
		color: #303030;
		.filter-icon {
			display: inline;
			margin: 0 5px;
			line-height: 35px;
			font-size: 18px;
		}
		.filter-name {
			display: inline;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.filter-details {

			&:before {
				content: " ";
				.clearfix
			}
			display: inline-block;
		}
		.close {
			position: absolute;
			height: 35px;
			line-height: 35px;
			right: 5px;
			font-size: 14px;
			opacity: 1;
			text-shadow: none!important;
			&:before{
				-moz-transition: all .2s ease-in;
				-o-transition: all .2s ease-in;
				-webkit-transition: all .2s ease-in;
				transition: all .2s ease-in;
				color: #9c9c9c;
				padding: 2px 4px 2.5px 4px;
				border-radius: 50%;
			}


		}
		&:hover .close:before {
			color: darken(#9c9c9c, 25);
		}
	}


}

.interests-results-table {

	.highlighted {
		background-color: #328df0 !important;
		color: white!important;
	}
}

.location-list {
	.filter-item {
		position: relative;
		display: inline-flex;
		padding: 0 70px 0 10px;
		height: 75px;
		line-height: 35px;
		border-radius: 20px;
		max-width: 95%;
		margin: 5px 2px;
		background-color: #e5e5e5;
		color: #303030;
		md-icon.text-white {
			margin-top: -5px;
			margin-right: 5px;
			height: 16px;
			width: 16px;
			min-height: 16px;
			min-width: 16px;
			svg {
				fill: @brand-white
			}
		}

		.filter-icon {
			display: inline;
			margin: 0 5px;
			line-height: 35px;
			font-size: 18px;
		}
		.filter-name {
			display: inline;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.filter-details {
			display: block;
			margin-top: -5px;
			line-height: 23px;
			max-width: 100%;
			text-overflow: ellipsis;
			word-wrap: normal;
			white-space: nowrap;
			overflow: hidden;
		}
		md-menu {
			position: absolute;
			right: 2px;
			top: 0px;
			margin-top: -10px;

			@media @upto-tablet {
				margin-top: 0px;
			}
			.md-button {
				background-color: inherit;

				padding: 0;
				height: 30px;
				width: 30px;
				min-height: inherit;
			}
			md-icon {
				height: 18px;
				width: 18px;
				min-height: 18px;
				min-width: 18px;
				svg {
					fill: #737373;
				}
			}
		}

		&.primary {
			background-color: @brand-warning;
			.filter-name {
				color: #505050;
				.text-muted {
					color: white;
					text-shadow: 1px 1px 1px rgba(0,0,0,0.3);
				}
			}

			md-menu svg {
				fill: #fff;
			}
		}

	}

}


