/**
   Component: layout.less
*/

@aside-wd:                                300px;

@header-hg:                               @navbar-height;
@footer-hg:                               60px;
@content-padding:                         15px;

@boxed-max-width:                         1140px;

@layout-transition-time:                  0.40s;

@material-space:                          40px;

@z-index-section:                         110;
@z-index-header:                          @z-index-section + 300;
@z-index-aside:                           @z-index-section + 200;
@z-index-footer:                          @z-index-section + 100;

html {
  height: 100%;
  // http://updates.html5rocks.com/2013/12/300ms-tap-delay-gone-away
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}
body {
  overflow: hidden;
  height: 100%;
}

// Main wrapper
// -----------------------------
.app-container {
  position: relative;
  width: 100%;
  min-height: 100%;
  height: auto;

  background-color: inherit;

  > header {
    position: relative;
    z-index: @z-index-header;
    box-shadow: 0 0 4px rgba(0,0,0,.14),0 4px 8px rgba(0,0,0,.28);
  }

  // Contains the main sidebar
  > aside {
    position: absolute;
    top: @header-hg;
    left: 0;
    bottom: 0;
    width: @aside-wd;
    box-shadow: 0 0 4px rgba(0,0,0,.14),2px 4px 8px rgba(0,0,0,.28);
    z-index: @z-index-aside;
    .transition(all @layout-transition-time ease 0s);
    will-change: margin;

  }

  // Contains the main content
  > section {
    height: 100%;
    margin-left: 0;
    z-index: @z-index-section;
    .transition(margin @layout-transition-time ease 0s);
    will-change: margin;
  }

  // Page Footer
  > footer {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: @footer-hg;
    padding: @content-padding;
    border-top: 1px solid @gray-lighter;
    background-color: @body-bg;
    z-index: @z-index-footer;
    .transition(margin @layout-transition-time ease 0s);
    will-change: margin;
    > p {
      margin: 0;
    }
  }

  @media @tablet {
      overflow: hidden;
    // move all to place the sidebar
    > section,
    > footer {
      margin-left: @aside-wd;
    }

  }
}


// App main content (where the view loads)
// -----------------------------
.app {

  padding-bottom: @footer-hg + 20; // ensure a space for the footer
  width: 100%;
  .container,
  .container-fluid {
    padding-left: 0;
    padding-right: 0;
  }

  // expand over wrapper padding
  .unwrap {
    margin: -@content-padding;
  }

}

.app-view-header {
  .clearfix;
  font-size: @font-size-h3;
  margin: 20px 0 30px 0;
  padding: 0;
  > small {
    display: block;
    font-size: 12px;
    color: @text-muted;
    line-height: 2;
    @media @mobile {
      display: inline-block;
    }
  }
  > button, > .btn {
    margin: 10px 10px 0 0;
  }
  @media @tablet {
    > button, > .btn {
      margin: 0;
    }
  }
}

// Aside offscreen layout
// -----------------------------

.aside-offscreen {
  .app-container {
    > section,
    > footer {
      margin-left: 0;
    }
    > aside  {
      margin-left: -@aside-wd;

    }
  }

}

@media @upto-tablet {
  .app-container {
    .sidebar:after {
      position: fixed;
      top: 0;
      bottom: 0;
      right: 0;
      left: @aside-wd;
      background-color: rgba(0, 0, 0, 0.51);
      pointer-events: all;
      opacity: 1.0;
      .transition(all @layout-transition-time ease 0s);
      .transition-delay(300ms);
      will-change: opacity;
    }
  }
  .aside-offscreen {
    .sidebar:after {
      opacity: 0.0;
      pointer-events: none;
      .transition(all 0s ease 0s);
    }
}
}

// HW accelerate transform for mobile
@media @upto-tablet {
  .csstransforms3d1 {
    .app-container {
      .backface-visibility(hidden);
      > aside {
        margin-left: 0;
        transform-style: preserve-3d;
        .translate3d(0,0,0);
        .transition-transform(@layout-transition-time);
      }
    }
    .aside-offscreen {
      .app-container {
        > aside {
          .translate3d(-@aside-wd,0,0);
        }
      }
    }
  }
}


// Fixed layout
// -----------------------------
.layout-fixed {

  .app-container {
    > aside,
    > header {
      position: fixed;
    }

    @media @upto-tablet {
      > aside {
        opacity: 0.90;
      }
    }

    > header {
      width: 100%;
    }
    > section {
      padding-top: @header-hg;
    }
  }
}

// Boxed layout
// -----------------------------
.layout-boxed {
  .app-container {
    > section {
      max-width: @boxed-max-width;
      margin: 0 auto;
    }
    > header {
      width: 100%;
    }
  }

  .container-fh {
    max-width: @boxed-max-width;
    margin: 0 auto;
  }
  .hidden-boxed {
    display: none;
  }
  .fw-boxed {
    width: 100%;
  }
}

// Dock Layout
// -----------------------------------

.layout-dock {
  .app-container {
    > section  {
      margin-left: auto;
      margin-right: auto;
      max-width: 1100px;
    }
  }
}

// Hidden Footer
// -----------------------------------

.footer-hidden {
  .container-fh {
    bottom: 0;
  }
  .app {
    padding-bottom: 0;
  }
  .app-container > footer {
    display: none;
  }
}


// Full Height Layout
// -----------------------------------
.app-fh {
  position: absolute;
  width: auto;
  overflow: auto;
  top: @header-hg;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 0;
  .transition(left @layout-transition-time ease);
  will-change: left;
  -webkit-overflow-scrolling: touch;

  @media @tablet {
    left: @aside-wd;
  }
  .aside-offscreen &,
  .layout-boxed & {
    left: 0;
  }
}
.container-fh {
  height: 100%;
}

// Table based layout
// -----------------------------------

// Table display types to use under .layout-fs
.l-table,
.l-table-fixed {
  display: table;
  width: 100%;
  height: 100%;
  min-height: 240px;
  border-spacing: 0;
}

.l-table-fixed {
  table-layout: fixed;
}

.l-row {
  display: table-row;
  height: 100%;
}

.l-cell,
.l-cell-wrapper {
  position: relative;
  display: table-cell;

  width: 100%;
  vertical-align: top;
  overflow: hidden;
}

// used as a variant to include
// content with variable height
.l-cell-wrapper {
  display: block;

}


/* Target Firefox only */
@-moz-document url-prefix() {
  .l-table, .l-table-desktop {
    height: 240px;
    min-height: 100%;
  }
  .l-cell-inner {
    overflow: auto;
  }
}

// layout helpers
.fh {
  height: 100% !important;
}
.fw {
  width: 100% !important;
}
.mfh {
  max-height: 100% !important;
}
.mfw {
  max-width: 100% !important;
}
.v-middle {
  vertical-align: middle;
}
.v-top {
  vertical-align: top;
}
.scroll {
  overflow: auto;
}
.scroll-y {
  overflow-y: auto;
}
.scroll-y {
  overflow-y: auto;
}



// -------------------------------------------------- */
/* Generic
// -------------------------------------------------- */

body {
  background: rgb(182,213,223);
  background: -moz-linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgb(182,213,223)), color-stop(100%, rgb(255,255,255)));
  background: -webkit-linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  background: -o-linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  background: -ms-linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  background: linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b6d5df', endColorstr='#ffffff', GradientType=1 );
  background-attachment: fixed;
}


// -------------------------------------------------- */
/* Scroll Styles
// -------------------------------------------------- */

.scroll-container {
  position: absolute;
  top: 50px;
  bottom: 0;
  left: 0;
  right: 0;
  @media @tablet {
    top: 0;
  }
}

.scroll-content {
  overflow:auto;
  height:100%;
  padding-bottom: 50px;
  @media @tablet {
   overflow-y: hidden;
  }
}

// fixed in full height w/ scroll
.fixed-top {
  display: block;
  position: fixed;
  top: 50px;
  left:0;
  right:0;
  width: 100%;
  padding-top: 10px;
  z-index: 300;
}

// -------------------------------------------------- */
/* Full Container 100%
// -------------------------------------------------- */

.full {
  height: 100%;
  position: absolute;
  width: auto;
  overflow: auto;
  top: 0;
  left: 0;
  right: 0;
  padding: 0;
}

.full-container {
  height: 100%;
  margin-bottom: -80px;
}

.full-content {
  position: absolute;
  height: 100%;
  width: 100%;
}

.under-nav .full-container {
  padding-top: @navbar-height;
}


.overflow-x-hidden {
  overflow-x: hidden;
}

.bottom-link {
  position: absolute;
  bottom: 0;
}

.bg-body {
  background: rgb(182,213,223);
  background: -moz-linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgb(182,213,223)), color-stop(100%, rgb(255,255,255)));
  background: -webkit-linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  background: -o-linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  background: -ms-linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  background: linear-gradient(45deg, rgb(182,213,223) 0%, rgb(255,255,255) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b6d5df', endColorstr='#ffffff', GradientType=1 );
  background-attachment: fixed;
}