/**
   Component: sidebar.less
*/
 
@scrollbar-wd:                    17px;

.sidebar-wrapper {
  height: 100%;
  overflow-x: hidden;
  background-color: inherit;  
  hr {
    margin: 10px 5px;
    border-top-color: rgba(0,0,0,.15);
  }
}

.sidebar {
  position: relative;
  height: 100%;
  width: @aside-wd + @scrollbar-wd;  // hides the scrollbar
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  background-color: inherit;
  &:after { content: "";}


  ul {
    &:before, &:after { display: none}
    height: 100%;

    width: 100%;
    display: table;
    li {
      display: table-row;
      height: auto;
      max-height: 75px;
      vertical-align: middle;
      position: relative;

      &:after {
        content: "";
        left: 0;
        right: 0;
        height: inherit;
        position: absolute;
        height: 1px;
        background: rgba(104, 104, 104, 0.3);
      }


      &:first-child:after {
        display: none;
      }
      &:hover:after, &:first-child:hover + :nth-child(2):after + {
        background: rgba(255, 255, 255, 0.3);
      }
      a, .clickable {
        height: 100%;
        min-height: 65px;
        width: 100%;
        display: inline-table;
        padding-left: 0;
        span {
          vertical-align: middle;
          height: 100%;
          width: 100%;
          display: inline-flex;
          display: -moz-box;
          display: -ms-inline-flexbox;
          display: -webkit-inline-flex;
          display: inline-flex;
          align-items: center;
          min-height: 45px;
          i, .sidebar-pic {
            font-size: 27px;
            margin: 0 25px;
          }
          .sidebar-pic {
            width: 46px;
            height: 46px;
            margin: 0 15px 0 16px;
            background-position: 50% 50%;
            background-size: cover;
            background-repeat: no-repeat;
            border-radius: 50%;
          }
          h3 {
            margin: 0;
            padding: 0;
            font-size: 14px;
            text-transform: uppercase;
            letter-spacing: 2px;
            font-weight: 400;
            font-family: DIN-Regular, sans-serif;
          }

          .pour-more {
            width: 100%;
          }
        }
      }
      * {
        display: inline-block;
      }
    }

    li.pour-more-container {
      &:after {
        background: rgba(104, 104, 104, 0.3)!important;
      }
      a, .clickable {
        min-height: inherit;
        max-height: none;
        img {
          max-width: 100%;
          margin: 0;
        }
      }
    }

  }
}

// Sub Levels
.sidebar-subnav {
  
  height: 0;
  margin-left: -20px;
  overflow: hidden;
  opacity: 0;
  .transition(all .2s ease-in-out 0s);

  > li  {
    > a {
      display: block;
      position: relative;
      padding: 10px 20px;
      padding-left: 40px;
      font-weight: normal;
      .transition(background-color .2s ease);
    }
  } // li

  > .sidebar-subnav-header {
    display: none;
    padding: 12px 15px;
    cursor: pointer;
    > a {
      padding: 0 !important;
      background-color: transparent !important;
    }
  }

}

.sidebar-toggle {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  min-width: 75px;
}


// Desktop styles
// ---------------------------------

@media @tablet {
  
  .sidebar > .sidebar-nav > .nav {
    padding-right: 0;
    .label,
    .sidebar-item-caret {
      margin: 2px 0 0 0;
      overflow: hidden;
    }
    .sidebar-item-caret + .label {
      margin-right: 5px;
    }

  }

} // @media @desktop



.pour-more {
  .flex-center;
  width: 100%;
  min-height: 50px;
  
  i {
    margin: 0 10px 0 auto!important;
    display:block;
    font-size:5em!important;
    color: @brand-success!important;
  }
  h2{
    margin: 0 auto 0 10px!important;
    display:block;
    color: @brand-success!important;
  }
}
