@-webkit-keyframes fadeIn { from { opacity:0; opacity: 1\9; /* IE9 only */ } to { opacity:1; } }
@-moz-keyframes fadeIn { from { opacity:0; opacity: 1\9; /* IE9 only */ } to { opacity:1; } }
@keyframes fadeIn { from { opacity:0; opacity: 1\9; /* IE9 only */ } to { opacity:1; } }
cardview {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 70px;
  overflow: hidden;
  &.has-ad-card {
    z-index: 10;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    opacity:0;  /* make things invisible upon start */
    -webkit-animation:fadeIn ease-in 1;  /* call our keyframe named fadeIn, use animattion ease-in and repeat it only 1 time */
    -moz-animation:fadeIn ease-in 1;
    animation:fadeIn ease-in 1;

    -webkit-animation-fill-mode:forwards;  /* this makes sure that after animation is done we remain at the last keyframe value (opacity: 1)*/
    -moz-animation-fill-mode:forwards;
    animation-fill-mode:forwards;

    -webkit-animation-duration:0.8s;
    -moz-animation-duration:0.8s;
    animation-duration:0.8s;
  }
}

#card-footer-container
{
  &.has-ad-card {
    display: none;
  }

}


.card-view {
  display:block;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  /* Prevent native touch events on Windows */
  -ms-touch-action: none;

  /* Prevent the callout on tap-hold and text selection */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  /* Prevent elements to be highlighted on tap */
  -webkit-tap-highlight-color: rgba(0,0,0,0);

  /* Prevent text resize on orientation change, useful for web-apps */
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  -ms-text-size-adjust: none;
  -o-text-size-adjust: none;
  text-size-adjust: none;


}

.card-headline {
  margin-bottom: 10px;
  h2 {
    display: inline-block;
  }
  p {
    display: inline-block!important;
    margin-left: 5px;

  }
}

.deck {
  position: relative;
  z-index: 10;
  width: 100%;
  height: 100%;
}

.card {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  /* Put the scroller into the HW Compositing layer right from the start */
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
}

.profile-card{
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;

  ul > li > a {
    box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
  }
  .panel-card {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    padding: 0;
    margin-bottom: 0;
    height: 100%;
  }
  .panel-heading {
    height: 100%;
    z-index: 0;
    background: transparent;
    padding: 0;
    &:after {
      box-shadow: none!important;
    }

    em {
      color: @brand-warning;
      margin-left: 5px;
      margin-top: 5px;
    }

    .card-text-overlay {
      /* NB: See 40% TODO: */
      max-height: 40%;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 8px;
      .linear-gradient(rgba(0,0,0,0),rgba(0,0,0,0.75));
      * {
        color: white;
      }
      .card-headline {
        margin-left: -8px;
        background-color: rgba(0, 0, 0, 0.35);
        padding: 2px 5px;
        h2 {
          margin: 5px 0;
        }
        p {
          display: block!important;
          font-size: 14px!important;

          margin: 0 0 5px 0!important;
        }

      }
      .overview {
        text-shadow: 0px 4px 3px rgba(0,0,0,0.5),
        0px 8px 13px rgba(0,0,0,0.2),
        0px 18px 23px rgba(0,0,0,0.2);
        h2 {
          text-transform: uppercase;
        }
        p {
          font-size: 14px;
          margin-right: 5px;
          display: inline;
        }
      }


      .could_use {
        margin-top: 10px;
        :first-child {
          border-right: 1px inset rgba(255, 255, 255, 0.80);
        }
        .col-xs-6 {
          margin: 0;
          padding: 2px;
          h5 {
            margin-top: 0;
            color: @brand-warning;
            border: none;
            margin-bottom: 4px;
            font-family: 'GothamRnd-Light', Helvetica, Arial, sans-serif;
            font-size: 11px;
            text-transform: capitalize;
            text-shadow: 1px 2px 2px rgba(0,0,0,0.4);
          }
          p {
            font-size: 12px;
            line-height: 20px;
            text-shadow: 1px 2px 2px rgba(0,0,0,0.4);
            &.headline {
              text-transform: uppercase;
              font-weight: bold;
              word-wrap: break-word;
            }
            &.detail {
              max-width: 100%;
              height: 52px;
              margin: 0 auto;
              font-size: 11px;
              -webkit-line-clamp: 3;
              -webkit-box-orient: vertical;
              overflow: hidden;
              text-overflow: ellipsis!important;
              display: inline;
              word-wrap: break-word;
            }
          }
        }
      }
    }
  }
}

.card-footer {
  width: 100%;
  height: 70px;
  margin: 0px;
  padding: 0px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  button {
    height: 100%;
    h3 {
      margin: 2px;
    }
    em {
      font-size: 20px;
      margin-right: 5px;
    }
  }
}

.divider{
  position:absolute;
  left:50%;
  top:5%;
  bottom:5%;
  border-left:1px solid @brand-success-dark;
  z-index: 1;
}
.card-contain {
  background: transparent no-repeat center 10%;
  .background-size(75%);
}

.card-cover, .card-cover #profilebackground {
  background: no-repeat center center;
  .background-size(cover);
}

.dashboard-button-success {
  &:hover, &:active, &:focus {
    background-color: @brand-success;
    box-shadow: none;
  }
}

.dashboard-button-white {
  color: @brand-success;
  background-color: @brand-white;
  &:hover, &:active, &:focus, &:disabled {
    background-color: @brand-white;
    box-shadow: none;
    color: @brand-success;
  }
}


md-grid-tile {
  transition: all 400ms ease-out 50ms;
  &.pyc-users {
    background: @brand-primary;
    &:before{
      content: '';
    }
  }
  &.pyc-networks {
    background: @brand-success;
    &:before{
      content: '';
    }
  }
  &.pyc-interests, &.pyc-location {
    background: @brand-danger;
    &:before{
      content: '';
    }
  }
  &.pyc-recommend {
    background: @brand-warning;
    &:before{
      content: '';
    }
  }

  .fa {
    color: #fff!important;
    font-size: 500%;
  }
}

/* Info on arrow key navigation */
.kicon:before,
.kicon:after {
  .fa
}
.info-keys {
  position: fixed;
  bottom: 30px;
  right: 180px;
  width: 60px;
  font-size: 8px;
  padding-top: 20px;
  z-index: 7000;
  text-transform: uppercase;
  color: @brand-success;
  letter-spacing: 1px;
  text-align: center;
}


.info-keys:before,
.info-keys:after {
  position: absolute;
  top: 0;
  width: 16px;
  height: 16px;
  border: 1px solid @brand-success;
  text-align: center;
  line-height: 14px;
  font-size: 12px;
}

.info-keys:before {
  left: 10px;
  content: "\f060";
}

.info-keys:after {
  right: 10px;
  content: "\f061";
}
