//
// Extras styles to use panel as widgets
// -------------------------------------

.carddeck {
  display: inline-block;
  float: left;
  padding: 15px;
  margin-bottom: 30px;
  transition: all 2s;
  min-height: 250px;

  &.card-placeholder {
    min-height: 194px;
    margin-top: 10px;
  }

  &.carddeck-details {
    min-height: inherit;
    width: 100%!important;
    overflow:hidden;
    cursor: pointer;
    padding: 0px;
  }

  @media @desktop-lg {
    width: 33.33%;
  }
  @media only screen and (max-width: 1399px) {
    width: 50%;
  }
  @media @upto-desktop {
    width: 100%;
  }

  .opendeck {
    opacity: 0;
    &.animate {
      -webkit-transform: translateY(200px);
      transform: translateY(200px);
      -webkit-animation: moveUp 0.65s ease forwards;
      animation: moveUp 0.65s ease forwards;
    }


  }
}

@-webkit-keyframes moveUp {
  0% { -webkit-transform: translateY(200px);
    transform: translateY(200px);opacity: 0;}
  100% { -webkit-transform: translateY(0); opacity: 1; }
}

@keyframes moveUp {
  0% {-webkit-transform: translateY(200px);
    transform: translateY(200px); opacity: 0;}
  100% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
}






@widget-margin: 15px;

.widget {
  padding: 10px;
  margin: @widget-margin 0;
  border: 0;
  width: inherit;
  &.panel,
  & .panel {
      overflow: hidden
  }
  .lateral-picture {
    position: relative;
    display: block;
    background-color: #f0f0f1;
    height: 190px;
    width: 100%;
    overflow: hidden;
    text-align: center;
    > .fa {
      line-height: 190px;
      width: 100%;
      font-size: 70px;
    }
  }
}



/**
 * The widget-card stack mixin.
 *
 * @param   color  $widget-card-color    Main color of the widget-card stack. Defaults to white.
 * @param   color  $shadow-color  Color of the shadow. Defaults to black.
 *
 * @return  box-shadow            Applies a box shadow to the element.
 */
.widget-card {
  background-color: white;
  word-wrap: break-word;
}
.widget-card.widget-card-primary, .widget-card.widget-card-dark {
  color: white;
}
.widget-card.widget-card-danger {
  background-color: tomato;
  text-shadow: 0 0.0625em 0 rgba(0, 0, 0, 0.25);
  box-shadow: 0 0.0625em 0.1875em 0 rgba(0, 0, 0, 0.1), 0 0.5em 0 -0.25em #f25e43, 0 0.5em 0.1875em -0.25em rgba(0, 0, 0, 0.1), 0 1em 0 -0.5em #e5593f, 0 1em 0.1875em -0.5em rgba(0, 0, 0, 0.1);
}
.widget-card.widget-card-primary {
  background-color: #47e3ff;
  box-shadow: 0 0.0625em 0.1875em 0 rgba(0, 0, 0, 0.1), 0 0.5em 0 -0.25em #43d7f2, 0 0.5em 0.1875em -0.25em rgba(0, 0, 0, 0.1), 0 1em 0 -0.5em #3fcce5, 0 1em 0.1875em -0.5em rgba(0, 0, 0, 0.1);
}
.widget-card.widget-card-success {
  background-color: #285d63;
  box-shadow: 0 0.0625em 0.1875em 0 rgba(0, 0, 0, 0.1),
  0 0.5em 0 -0.25em #255056, 0 0.5em 0.1875em -0.25em rgba(0, 0, 0, 0.1),
  0 1em 0 -0.5em #255056, 0 1em 0.1875em -0.5em rgba(0, 0, 0, 0.1);
}

.widget-card.widget-card-dark {
  background-color: #404040;
  box-shadow: 0 0.0625em 0.1875em 0 rgba(0, 0, 0, 0.1), 0 0.5em 0 -0.25em #3c3c3c, 0 0.5em 0.1875em -0.25em rgba(0, 0, 0, 0.1), 0 1em 0 -0.5em #393939, 0 1em 0.1875em -0.5em rgba(0, 0, 0, 0.1);
}
.widget-card > :last-child {
  margin-bottom: 0;
}

.widget-card-placeholder {
  cursor: pointer;
  background-color: transparent;
  border: 2px dashed @brand-success-light;
  border-radius: 5px;
  color: @brand-success-light;
  .lateral-picture {
    background-color: transparent;
    .fa {
      line-height: normal;
      color: @brand-success-light;
      margin-top: 15px;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
    }
  }

  &:hover {
    .lateral-picture > .fa {
      .scale(1.3, 1.3)
    }
  }
}


.stack {
  margin: 10px auto;
  width: 100%;
  padding: 0;
  position: relative;
  max-width: 100%;
}

.stack .widget {
  max-width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  outline: 1px solid transparent;
}

.stack .widget:last-child {
  position: absolute;
  cursor: pointer;
  margin-top: 20px;
  box-shadow: 0 0.0625em 0.1875em 0 rgba(0, 0, 0, 0.1), 0 0.6em 0 -0.35em #f2f2f2,
  0 0.5em 0.1875em -0.25em rgba(0, 0, 0, 0.1), 0 1.1em 0 -0.6em #e5e5e5,
  0 1em 0.1875em -0.5em rgba(0, 0, 0, 0.1);
}

.stack-randomrot:hover .widget:last-child {
  box-shadow: none;
}

.stack-randomrot:hover .widget:nth-child(3) {
  -webkit-transform: translate(11px,11px);
  transform: translate(11px,11px);
}

.stack-randomrot:hover .widget:first-child {
  -webkit-transform: rotate(-6deg);
  transform: rotate(-6deg);
}

.stack-randomrot:hover .widget:nth-child(2) {
  -webkit-transform: rotate(6deg);
  transform: rotate(6deg);
}



@media @upto-tablet {
  .stack {
    margin: 0!important;
    .widget:last-child {
      margin-top: 0!important;
    }
  }

  .widget {
    margin: 0!important;
  }

  .carddeck {
    margin: 0 auto;
    display: block;
  }
}




