.add-on-input {
	display: flex;
	margin-bottom: 1em;
}

.add-on-input-field {
	flex: 1;
}
.add-on-input-field:not(:first-child) {
	border-left: 0;
}
.add-on-input-field:not(:last-child) {
	border-right: 0;
}

.add-on-input-item {
	background-color: rgb(225, 225, 225);
	color: #8c8c8c;
	font: inherit;
	font-weight: normal;

	span.active::before {
		opacity: 0;
	}
}

.add-on-input-field,
.add-on-input-item {
	border: 1px solid rgba(147, 128, 108, 0.25);
	padding: 0.5em 1.2em;
}
.add-on-input-field:first-child,
.add-on-input-item:first-child {
	border-radius: 2px 0 0 2px;
}
.add-on-input-field:last-child,
.add-on-input-item:last-child {
	border-radius: 0 2px 2px 0;
}

.add-on-results {
	background-color: white;
	padding: 0;
	border: 1px solid rgba(147, 128, 108, 0.35);
	border-radius: 10px;
	list-style: none;

	.add-on-results-item {
		padding: 10px;
		&:first-child {
			background-color: #328df0;
			color: white;
			border-top-left-radius: 10px;
			border-top-right-radius: 10px;
		}
		&:last-child {
			border-bottom-left-radius: 10px;
			border-bottom-right-radius: 10px;
		}
		h4 {
			font-size: 15px;
			text-overflow: ellipsis;
			word-wrap: normal;
			white-space: nowrap;
			overflow: hidden;
			span {
				margin-left: 5px;
			}
		}
	}
}

.add-on-list {
	background-color: transparent;
	padding: 0;
	border-radius: 10px;
	list-style: none;

	.add-on-list-item {
		position: relative;
		padding: 0px 5px 0px 55px;
		border: 1px solid rgba(147, 128, 108, 0.20);
		margin-bottom: 10px;
		border-radius: 10px;
		h4 {
			font-weight: bolder;
			color: #000;
			margin-top: 8px;
			font-size: 15px;
			text-overflow: ellipsis;
			word-wrap: normal;
			white-space: nowrap;
			overflow: hidden;
			span {
				margin-left: 5px;
			}
		}
		
		.remove-list-item {
			position: absolute;
			right: 10px;
			top: 50%;
			margin-top: -12px;
			em {
				cursor: pointer;
				font-size: 20px;
				color: @brand-danger;
			}
		}
	}
}