/**
   Component: top-navbar.less
*/

@nav-header-wd:                 @aside-wd;


// Navbar Mobile General styles
// ------------------------------

.topnavbar {
  position: relative;
  margin-bottom: 0;
  border-radius: 0;
  border: 0;
  .transition(box-shadow .2s ease);
  -webkit-backface-visibility: hidden; /* chrome jump */

  // Navbar Header (logo)
  // -----------------------------------
  .navbar-header {
    @media @upto-tablet {
      position: absolute;
    }
    position: relative;
    width: 100%;
    .transition(width .2s ease);

    @media @tablet {
      background-image: none;
      z-index: 1; // overlaps wrapper
    }

    > .navbar-brand {
      position: relative;
      display: inline;
      padding: 0 5px;
      width: 50px;
      line-height: @header-hg;
      z-index: 1;
      img {
        max-height: 100%;
        width: auto;
      }
    }

    // Different Logo size depends on the navbar state
    .brand-logo {
      display: inline-block;
    }

  }

  // relocate points for single notifications

  .nav > li > a .point-pin {
    display: inline-block;
    > .point {
      left: auto;
      right: -5px;
      bottom: auto;
      top: -5px;
    }
  }

  // Mobile button toggles
  // -----------------------------------
  .mobile-toggles {
    .clearfix();
    position: absolute !important;
    right: 0;
    left: 0;
    top: 0;
    height: @header-hg;
    line-height: @header-hg;
    padding: 0 10px;
  }

  // Offcanvas sidebar &
  // slide down menu toggle button
  .sidebar-toggle,
  .menu-toggle {
    font-size: 24px;
    z-index: 3001;
  }
  .sidebar-toggle {
    float: left;
    margin-left: -5px;
    margin-top: 2.5px;
  }
  .menu-toggle {
    float: right;
  }


  // Navbar items shadow style
  @media @tablet {
    .nav > li > a {
      &:hover, &:focus {
        background-color: fade(@brand-inverse, 5);
      }
    }
  }


}

// contains elements below header
.nav-wrapper {
  padding: 0 15px;
  // box-shadow: 0 1px 1px rgba(0,0,0,.1);

  // when collapse is opened
  &.in {
    overflow: visible;
  }

  // fullwidth dropdowns
  .navbar-nav .open .dropdown-menu {
    position: absolute;
    // background-color: rgb(255, 255, 255);
    left: 0px;
    right: 0px;
    border-top: 1px solid #e1e1e1;
    border-bottom: 1px solid #e1e1e1;
  }

  @media @tablet {
    .clearfix();
    position: relative;

    // restore opened dropdown
    .navbar-nav .open .dropdown-menu {
      left: auto;
      right: auto;
    }
    .navbar-nav.navbar-right .open .dropdown-menu {
      left: auto;
      right: 0;
    }

  }
}



.navbar-header {
  transition: width 0.2s ease 0s;
  width: 100%;
  position: fixed;

  @media @tablet {
    float: none;
    display: inline;
    width: @aside-wd;
  }
  .navbar-brand {
    padding: 3px;
    float: none;
    img, em {
      display: inline;
      height: 36px;
      margin: 7px;
      &.brand-logo {
        padding: 3px;
      }
        @media @tablet {
        &.brand-logo {
          display: none!important;
        }
        &.brand-logo-full {
          display: inline!important;
          height: @material-space;
          margin-top: -40px;
        }
      }
    }
  }
}

// -------------------------------------------------- */
/* Navbar Icons with badge numbers
// -------------------------------------------------- */
.nav-button-toggles{
  width: 100%;
  height: 100%;
  position: relative;

  .navbar-brand{
    padding: 11px 10px 10px;
  }

  .point-pin {
    .point {
      position: absolute;
      height: 7px;
      width: 7px;
      background-color: @brand-warning;
      color: @brand-white;
      border-radius: 50%;
    }
  }

  .notifications {
    position: absolute;
    right: 5px;
    #bell_icon {
      font-size: 2em;
      color: @brand-success;
      .point {
        top: 0%;
        right: 20%;
      }
    }
  }
  .messages {
    position: absolute;
    right: 50px;
    #message_icon {
      font-size: 2em;
      color: @brand-success;
      .point {
        top: 0%;
        right: 5%;
      }
    }
  }


  .navicon-button {
    display: inline-block;
    position: relative;
    padding: 2.0625rem 1.5rem;
    .transition(0.35s ease-in-out);
    cursor: pointer;
    .user-select(none);
  }
  .navicon-button .navicon:before, .navicon-button .navicon:after {
    .transition(0.35s ease-in-out);
  }

  .navicon {
    position: relative;
    width: 3.2rem;
    height: 2px;
    background: @brand-success;
    .transition(0.5s ease-in-out);
    border-radius: 0;

    &:before, &:after {
      display: block;
      content: "";
      height: 2px;
      width: 3.2rem;
      background: @brand-success;
      position: absolute;
      z-index: -1;
      .transition(0.5s 0.35s ease-in-out);
      border-radius: 0;
    }
    &:before{
      top: .755rem;
    }
    &:after {
      top: -.755rem;
    }
  }

  .open:not(.steps) .navicon:before,
  .open:not(.steps) .navicon:after {
    top: 0 !important;
  }

  .open .navicon:before,
  .open .navicon:after {
    .transition(0.5s ease-in-out);
  }

  /* Minus */
  .open {
    .scale(0.90);
  }
  /* Arrows */
  .open.to-arrow .navicon:before, .open.to-arrow .navicon:after {
    width: 1.5rem;
  }
  .open.to-arrow .navicon:before {
    .rotate(35deg);
    .transform-origin(left top);
  }
  .open.to-arrow .navicon:after {
    .rotate(-35deg);
    .transform-origin(left bottom);
  }
}

@header-offset: @header-hg + @material-space;

.layout-material {
  .layout-fixed;

  @media @tablet {
    .app-container {
      > header {
        z-index: @z-index-section - 1;
        > nav {
          background-color: @brand-success;
          padding-top: @material-space;
          .nav-button-toggles {
            .sidebar-toggle {
              display: none;
            }
            .navbar-brand {
              padding: 2.5px 10px;
            }
            .notifications {
              position: absolute;
              right: 15px;
              top: -20px;
              #bell_icon {
                  font-size: 40px!important;
                  color: @brand-white;
                .point {
                  top: 0%;
                  right: 17%;
                  height: 9px;
                  width: 9px;
                }
              }
            }
            .messages {
              position: absolute;
              right: 80px;
              top: -20px;
              #message_icon {
                font-size: 40px!important;
                color: @brand-white;
                .point {
                  top: 0%;
                  right: 5%;
                  height: 9px;
                  width: 9px;
                }
              }
            }
          }
        }
      }
      > aside {
        top: @header-offset;
        z-index: @z-index-section - 2;
        margin-left: 0 !important;
      }
      > section {
        position: absolute;
        padding-top: 0;
        top: 0;
        right: 0;
        left: @aside-wd;
        margin-top: @header-offset;
        margin-left: 0;
        max-width: none;
        z-index: 1;
        height: ~"calc(100% - (@{header-offset}))";
      }
      > footer {
        display: none;
      }
    }
  }
}