.tour-dialog {
  h2 {
    color: @brand-success;
  }

  p {
    font-size: 16px;
  }
}

.walkthrough-hole-glow {
  position: absolute;
  outline: none;
  border: 2px solid #FFFF66 !important;
  box-shadow: 0 0 36px #FFFF66 !important;
  -webkit-appearance: none;
  box-sizing: border-box;
}

.walkthrough-background {
  position:absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: initial;
  text-align: center;
  -webkit-transition: height 0s ease-out .2s, opacity .2s ease-out;
  -moz-transition: height 0s ease-out .2s, opacity .2s ease-out;
  -o-transition: height 0s ease-out .2s, opacity .2s ease-out;
  transition: height 0s ease-out .2s, opacity .2s ease-out;
  opacity:0;
  height: 0;
  overflow: hidden;
  z-index: 1000;
  pointer-events: all;
}

.walkthrough-hole {
  position: absolute;
  -moz-box-shadow: 0 0 0 2200px rgba(0, 0, 0, .7);
  -webkit-box-shadow: 0 0 0 2200px rgba(0, 0, 0, .7);
  box-shadow: 0 0 0 2200px rgba(0, 0, 0, .7);
  -webkit-appearance: none;
}

.walkthrough-element.walkthrough-text{
  max-height: 300px;
  z-index: 600;
  min-width: 300px;
  border-radius: 5px;
  font-family:  'DIN-Light', "Times New Roman", Times, serif;
  display: inline;

  color: #fff;
  font-size: 22px;
  background-color: transparent;
  overflow: hidden;
  font-weight: 400;
  line-height: 35px;
  padding: 35px;
  text-align: center;
  font-family: inherit;
  margin: 0;
  opacity: 1;
  position: absolute;
}

.walkthrough-element.walkthrough-done-button{
  position:absolute;
  width: 80px;
  display: block;
  margin: 0 auto 0 0;
  border: 1px solid rgba(255, 255, 255, 0.55) !important;
  box-shadow: 0 0 10px #FFF !important;
  .btn-sm;



  &.bottom {
    bottom: 100px;
    left:0;
    right:0;
    margin: 0 auto;
  }
}



.hide {
  opacity: 0;
}

.walkthrough-button-positive{
  border-color: #0c63ee;
  background-color: #387ef5;
  color: #fff;
}

.walkthrough-button-positive:hover {
  color: #fff;
  text-decoration: none;
}

.walkthrough-button-positive.active {
  border-color: #0c63ee;
  background-color: #0c63ee;
  box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.1);
}

.walkthrough-element.walkthrough-icon {
  height: 200px;
}
.dialog-icon {
  position: absolute!important;
  bottom: 20px!important;
  height: 125px!important;
  display: block!important;
  margin:0 auto!important;
  left: 0!important;
  right:0!important;
  -webkit-animation:movingRight 2s infinite;
  &.left {
    -webkit-animation:movingLeft 2s infinite;
  }
}
@-webkit-keyframes movingRight{
  0%{left:-10%;}
  50%{opacity:0.5;}
  100%{left:10%;opacity: 0}
}

@-webkit-keyframes movingLeft{
  0%{left:10%;}
  50%{opacity:0.5;}
  100%{left:-10%;opacity: 0}
}

.walkthrough-element.walkthrough-arrow{
  color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.walkthrough-element{
  z-index: 1001;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  pointer-events: none;
}

.walkthrough-background.walkthrough-active{
  -webkit-transition: opacity .2s ease-out;
  -moz-transition: opacity .2s ease-out;
  -o-transition: opacity .2s ease-out;
  transition: opacity .2s ease-out;
  opacity:1;
  height: 100%;
}

.walkthrough-transclude{
  position: relative;
  z-index: 1001;
}

.walkthrough-hole-round{
  border-radius: 200px;
}

.walkthrough-tip-text-box {
  /*top: 128px;*/
  position: relative;
  margin-left: 16px;
  margin-right: 16px;
  border: 2px solid;
  border-radius: 35px;
  padding: 14px;
  /*margin-top: 0;*/
  /*margin-bottom: 0;*/
}

.walkthrough-container{
  float: left;
  position: relative;
  height: 100vh;
  width: 100%;
  pointer-events: none;
}

.walkthrough-inner{
  position: absolute;
  width: 100%;
  pointer-events: none;
}
md-backdrop._md-dialog-backdrop {
  z-index: 1000;
}
.walkthrough-container-transparency > .walkthrough-inner{
  height: 100%;
}

.walkthrough-text-container{
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.walkthrough-container-tip > .walkthrough-top, .walkthrough-container-transparency > .walkthrough-top .walkthrough-text-container{
  top: 15px;
}

.walkthrough-container-tip > .walkthrough-bottom {
  bottom: 0;
}

/* take 'done' button into consideration */
.walkthrough-container-transparency > .walkthrough-bottom .walkthrough-text-container{
  bottom: 70px;
}

.walkthrough-tip-icon-image-front{
  z-index: 1002;
}

.walkthrough-tip-icon-image-back{
  z-index: 999;
}

.walkthrough-tip-icon-text-box {
  height: 142px;
  /*right: 9%;*/
  position: relative;
  margin-bottom: -32px;
  margin-right: -250px;
  /*bottom: 70px;*/
}

.walkthrough-tip-done-button-text-box{
  /*top: 109px;*/
  /*bottom: 59px;*/
  position: relative;
  z-index: 1002;
  /*right: -7px;*/
  margin-top: 107px;
  background-color: transparent;
  border: 0;
  float: right;
}

.walkthrough-tip-button-image-text-box{
  width: 42px;
  height: 42px;
}

.walkthrough-tip-text-box-color-black {
  border-color: #ffffff;
  background-color: #000000;
  color: #ffffff;
}

.walkthrough-tip-text-box-color-white {
  border-color: #000000;
  background-color: #ffffff;

}

.enjoyhint_close_btn, .enjoyhint_close_btn::after, .enjoyhint_close_btn::before, .enjoyhint_next_btn, .enjoyhint_skip_btn {
  -o-text-overflow: clip;
  text-overflow: clip
}

