.enjoyhint_close_btn {
  display: inline-block;
  position: absolute;
  box-sizing: content-box;
  width: .3em;
  height: .3em;
  border: none;
  -webkit-border-radius: 1em;
  border-radius: 1em;
  font: 400 8em/normal Arial, Helvetica, sans-serif;
