
  .settings-wrapper {
    display: none;
  }
  .in-app .settings-wrapper {
    display: block;
  }

  .settings-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 400;
    .transition(right .3s @easeInOutQuint);
    .settings-button {
      width: 50px;
      z-index: 15;
      height: 50px;
      line-height: 50px;
      background-color: @brand-primary;
      border: inherit;
      border-top: none;
      border-right: #fff;
      cursor: pointer;
      text-align: center;

      .transition(~"top .3s ease .1s, right .3s ease .1s, box-shadow 0s linear .3s, border .3s linear .3s, background 0s linear .3s");
      .shadow-z2;
      > em {
        font-size: 20px;
        color: #fff;
        &:before {
          .transition(content 0s linear .4s);
        }
      }
    }
  }

  .settings-content {
    input {
      background-color: transparent!important;
      width: 246px!important;
      border: none!important;
      box-shadow: none!important;
    }
  }

@media @upto-tablet {
  .settings-wrapper {
    position: absolute;
    top: @navbar-height;
    right: 0;
  }
}




.setting-color {
  padding: 0 5px;

  > label {
    display: block;
    position: relative;
    height: 40px;
    margin: 0 10px;
    border-radius: 3px;
    overflow: hidden;
    border: 1px solid rgba(0,0,0,.1);
    cursor: pointer;
    border: 1px solid @gray;
    
    &:first-child { margin-left: 0}
    &:last-child { margin-right: 0}

    .list-table {
      height: 100%;
      margin: 0;
    }
    
    @baseHg: 15px;
    > .color {
      display: block;
      height: @baseHg * 1.2;
    }
    
    > .split {
      .clearfix;
      display: block;
      > .color {
        display: block;
        height: @baseHg * 2.5;
        &:first-child {
          float: left;
          width: 70%;
        }
        &:last-child {
          float: right;
          width: 30%;
        }
      }
    }

    // icon checked
    > .icon-check {
      position: absolute;
      display: block;
      left: 50%;
      top: 50%;
      width: 20px;
      height: 20px;
      margin-top: -10px;
      margin-left: -10px;
      text-align: center;
      font-size: 1.33333333em;
      vertical-align: -15%;
      color: #fff;
      opacity: 0;
    }

    > input[type="radio"] {
      position: absolute;
      opacity: 0;
      visibility: hidden;
      &:checked + .icon-check {
        opacity: 1 !important;
      }
    }
  }
}

.settings-btn {
  .btn-xs;
  margin: 3px;
  padding: 4px;
  font-size: 13px;
  background-color: transparent;
  border-radius: 2px;
  display: inline-block;
  font-size: 90%;
  box-shadow: none;
  cursor: pointer;
  &.settings-btn-success{
    border: 1px solid @brand-success;
    color: @brand-success;
    &:hover {
      background-color: @brand-success;
      color: #ffffff;
    }
  }
  &.settings-btn-primary{
    border: 1px solid @brand-primary;
    color: @brand-primary;
    &:hover {
      background-color: @brand-primary;
      color: #ffffff;
    }
  }
}



.btn-switch {
  position: relative;
  display: inline-block;
  cursor: pointer;

  > input[type="checkbox"]  {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    cursor:pointer;
    left:-100%;
    top:-100%;

    + .btn {
      background-color: transparent;
      border-color: @brand-primary;
      color: @brand-primary ;
      text-align: left;
      > em {
        position: relative;
        top: -1px;
        margin-right: 2px;
        display: inline-block;
        border: 1px solid @brand-primary;
        border-radius: 50%;
        padding: 2.5px;
        margin: 0 4px 0 0;
        font-size: 10px;
        text-align: center;
        &:before {
          position: relative;
          top: 1px;
          opacity: 0;
        }
      }
    }
    &:checked + .btn {
      background-color: @brand-primary;
      color: #fff;
      > em {
        background-color: #fff;
        color: @brand-primary;
        &:before {
          opacity: 1;
        }
      }
    }
  }
}


.btn-switch-primary   { .btn-switch-variant(@brand-primary);   }
.btn-switch-success   { .btn-switch-variant(@brand-success);   }
.btn-switch-info      { .btn-switch-variant(@brand-info);      }
.btn-switch-warning   { .btn-switch-variant(@brand-warning);   }
.btn-switch-danger    { .btn-switch-variant(@brand-danger);    }
.btn-switch-inverse   { .btn-switch-variant(@brand-inverse);   }
.btn-switch-amber     { .btn-switch-variant(@brand-amber); }
.btn-switch-pink      { .btn-switch-variant(@brand-pink);      }
.btn-switch-purple    { .btn-switch-variant(@brand-purple);    }
.btn-switch-orange    { .btn-switch-variant(@brand-orange);    }

.btn-switch-variant(@color) {

  & > input[type="checkbox"]  {

    + .btn {
      border-color: @color;
      color: @color !important;
      > em {
        border-color: @color;
      }
    }
    &:checked + .btn {
      background-color: @color;
      color: #fff !important;
      > em {
        color: @color !important;
      }
    }
  }

}





