// -------------------------------------------------- */
/* Input Styles
// -------------------------------------------------- */
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px white inset;
}
.pycform-input {
  .input {
    position: relative;
    z-index: 1;
    display: inline-block;
    margin: 0.5em 0;
    width: 100%;
    vertical-align: top;
  }

  .input__field {
    /* Temporary Underline */
    border-bottom: 1px solid #285d63;

    position: relative;
    display: block;
    float: right;
    padding: 0.8em;
    width: 60%;
    border: none;
    border-radius: 0;
    background: transparent;
    color: #303030;
    font-weight: 400;
    font-family: "Avenir Next", "Helvetica Neue", Helvetica, Arial, sans-serif;
    -webkit-appearance: none; /* for box shadows to show on iOS */

  }

  .input__field:focus {
    outline: none;
  }

  .input__label {
    display: inline-block;
    float: right;
    padding: 0 1em;
    width: 40%;
    color: @brand-success;
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .input__label-content {
    position: relative;
    display: block;
    padding: 1.6em 0;
    width: 100%;
  }

  .graphic {
    position: absolute;
    top: 0;
    left: 0;
    fill: none;
  }

  .icon {
    color: #ddd;
    font-size: 150%;
  }

  /* Normal (w/ line underneath) */
  .input__field--normal {
    /* Temporary Underline */
    border-bottom: 1px solid #285d63;

    width: 100%;
    background: transparent;
    padding: 25px 0 0 15px;
    margin-bottom: 2em;
    font-size: 16px;
    font-weight: bold;
    color: @brand-success;
    -webkit-text-fill-color: @brand-success;
  }

  .input__label--normal {
    width: 100%;
    position: absolute;
    text-align: left;
    font-size: 22px;
    padding: 0 10px 20px 15px;
    pointer-events: none;
    color: #285D63;
    font-weight: 100;
    pointer-events: none;
    color: @brand-success;
  }

  //.input__label--normal::after {
  //  content: '';
  //  position: absolute;
  //  width: 100%;
  //  height: 2px;
  //  background: @brand-success;
  //  left: 0;
  //  top: 100%;
  //  -webkit-transform-origin: 50% 100%;
  //  transform-origin: 50% 100%;
  //  -webkit-transition: -webkit-transform 0.3s, background-color 0.3s;
  //  transition: transform 0.3s, background-color 0.3s;
  //}

  .input__label-content--normal {
    padding: 0;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transition: -webkit-transform 0.3s, color 0.3s;
    transition: transform 0.3s, color 0.3s;
  }

  //.input__field--normal:focus + .input__label--normal::after,
  //.input--filled .input__label--normal::after {
  //  background: @brand-success;
  //  -webkit-transform: scale3d(1, 0.85, 1);
  //  transform: scale3d(1, 0.85, 1);
  //}

  .input__field--normal:focus + .input__label--normal .input__label-content--normal,
  .input--filled .input__label--normal .input__label-content--normal {
    -webkit-text-fill-color: @brand-success;
    color: @brand-success;
    -webkit-transform: translate3d(-15px, 3em, 0) scale3d(0.655, 0.655, 1);
    transform: translate3d(-15px, 3em, 0) scale3d(0.655, 0.655, 1);
  }

  /* Empty (w/o line underneath) */
  .input.input--empty {
    margin-bottom: 25px;
  }

  .input__field--empty {
    width: 100%;
    background: transparent;
    padding: 0;
    margin-bottom: 2em;
    font-size: 16px;
    font-weight: bold;
    color: @brand-success;
    -webkit-text-fill-color: @brand-success;
  }

  .input__label--empty {
    width: 100%;
    left: 0;
    right: 0;
    position: absolute;
    text-align: center;
    font-size: 22px;
    padding: 0;
    pointer-events: none;
    // color: #285D63;
    font-weight: 100;
    pointer-events: none;
    color: @brand-success;
  }

  //.input__label--empty::after {
  //  content: '';
  //  position: absolute;
  //  height: 2px;
  //  background: transparent;
  //  left: 0;
  //  right: 0;
  //  width: 100%;
  //  top: 100%;
  //  -webkit-transform-origin: 50% 100%;
  //  transform-origin: 50% 100%;
  //  -webkit-transition: -webkit-transform 0.3s, background-color 0.3s;
  //  transition: transform 0.3s, background-color 0.3s;
  //}

  .input__label-content--empty {
    padding: 0;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transition: -webkit-transform 0.3s, color 0.3s;
    transition: transform 0.3s, color 0.3s;
  }

  //.input__field--empty:focus + .input__label--empty::after,
  //.input--filled .input__label--empty::after {
  //  background: @brand-success;
  //  -webkit-transform: scale3d(1, 0.85, 1);
  //  transform: scale3d(1, 0.85, 1);
  //}

  .input__field--empty:focus + .input__label--empty .input__label-content--empty,
  .input--filled .input__label--empty .input__label-content--empty {
    color: @brand-success;
    -webkit-transform: translate3d(-15px, 2em, 0) scale3d(1, 0.655, 1);
    transform: translate3d(-15px, 2em, 0) scale3d(0.655, 0.655, 1);
  }
}

textarea.textarea-success, textarea.textarea-success:focus {
	background-color: transparent;
	border-color: #285d63;
	border-width: 2px;
	font-size: 16px;
	font-weight: bold;
	color: @brand-success;
	font-family: "Avenir Next", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

label.success-label {
    background: transparent;
    padding: 15px 0 0 10px;
    font-size: 20px;
    color: @brand-success;
    small {
    	font-size: 14px;
    }
}

// -------------------------------------------------- */
/* Image Input
// -------------------------------------------------- */
.fileinput-button {
  position: relative;
  overflow: hidden;
  &:hover, :focus, :active {
    box-shadow: none;
    color: @brand-success-dark;
  }
}
.fileinput-button input {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  opacity: 0;
  -ms-filter: 'alpha(opacity=0)';
  font-size: 200px;
  direction: ltr;
  cursor: pointer;
}


// xEditable
.editable-wrap {
  display:block;
  white-space:nowrap;
  margin:0;
}

.editable-wrap .editable-controls,
.editable-wrap .editable-error {
  margin-bottom:0;
}

.editable-wrap .editable-controls>input,
.editable-wrap .editable-controls>select,
.editable-wrap .editable-controls>textarea {
  margin-bottom:0;
}

.editable-wrap .editable-input {
  display:block;
  width: 100%;
}

.editable-buttons {
  text-align: center;
  display:block;
  margin: 15px 0;
  vertical-align:top;
  button {
    width: 30%;
  }
  button:first-child {
    margin-right: 15px;
  }
}

.verified {
  border-bottom: dashed 1px;
  display: inline !important;
  color:#2a6496;
}

.unverified {
  border-bottom: dashed 1px;
  display: inline !important;
  color:@brand-warning;
}



.editable-input.editable-has-buttons {
  width: 100%;
}

.editable-bstime .editable-input input[type=text] {
  width:46px;
}

.editable-bstime .well-small {
  margin-bottom:0;
  padding:10px;
}

.editable-range output {
  display:inline-block;
  min-width:30px;
  vertical-align:top;
  text-align:center;
}

.editable-color input[type=color] {
  width:50px;
}

.editable-checkbox label span,
.editable-checklist label span,
.editable-radiolist label span {
  margin-left:7px;
  margin-right:10px;
}

.editable-hide {
  display:none!important
}

.editable-click,
a.editable-click {
  text-decoration:none;
  color:#428bca;
  border-bottom:dashed 1px #428bca;
}

.editable-click:hover,
a.editable-click:hover {
  text-decoration:none;
  color:#2a6496;
  border-bottom-color:#2a6496;
}

.editable-empty,.editable-empty:hover,
.editable-empty:focus,a.editable-empty,
a.editable-empty:hover,a.editable-empty:focus {
  font-style:italic;
  color:#D14;
  text-decoration:none
}




.pyc-select {
  background-color: transparent;
  border: none;
  margin-top: 10px;
  margin-bottom: 60px;
  div {
    .ui-select-match {

    }
