/* Width & Height Styles
// -------------------------------------------------- */
.background-size(@size){
  -webkit-background-size: @size;
  -moz-background-size: @size;
  -o-background-size: @size;
  background-size: @size;
}
.inline-lg {
  display: inline;
}

@media @upto-desktop {
  .inline-lg {
    display: block;
    margin: 0 auto;
  }
  .text-center-sm {
    text-align: center;
  }
}

.img-rounded {
  border-radius: 50%;
}
.text-italic {
  font-style: italic;
}
.form-control[disabled] {
  color: rgba(0, 0, 0, 0.48);
  font-style: italic;
}
.text-success-disabled {
  color: @brand-success;
}
.overflow-hidden {
  overflow: hidden!important;
}
.all-caps {
  text-transform: uppercase;
}
.lh-50 {
  line-height: 50px;
}
.flex-center {
  display: flex!important;
  align-items: center;
  img {
    display: block;
    margin: 0 auto;
  }
}

.close-out {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 0 20px 20px;
  z-index: 35;
}

.relative {
  position: relative;
}


/* Sign-up Privacy Policy Fix */

.fixed-stacked-bottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 70px;
  z-index: 12;
  font-size: 18px;
}

.fixed-bottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 11;
}

.height-40 {
  height: 40px;
}

.height-50 {
  height: 50px;
}

.height-70 {
  height: 70px;
}

.height-90 {
  height: 90px;
