// /* Guest
// -------------------------------------------------- */
.guest-container {
	min-height: 568px;
	height: 100%;
	overflow: hidden;
}
#introload {
	height: 100%;
	top: 0;
	bottom: 0;
}

.intro-logo, #introload {
	position: absolute;
	left: 0;
	right: 0;
	width: 100%;
}
.intro-logo {
	top: 35%;
	-ms-transform: translateY(-50%);
	-webkit-transform: translateY(-50%);
	-moz-transform: translateY(-50%);
	-o-transform: translateY(-50%);
	transform: translateY(-50%);
}

.intro-text {
	vertical-align: middle;
	color: @brand-success;
	text-align: center;
	h3 {
		font-family: DIN-Regular, sans-serif;
		margin: 30px 0 5px 0;
	}
	p {
		font-size: 15px;
	}

	.inline {
		padding-bottom: 8px;
	}
}

.intro-form {
	.full-container {
		padding-top: 50px;
		margin-bottom: -50px;
	}
}

.intro-col {
	width: 100%;
	max-width: 350px !important;
	min-height: 20px;
	margin-left: auto;
	margin-right: auto;
}

.forgot-password {
	font-family: DIN-Regular, sans-serif;
	color: @brand-success;
	margin: -20px 10px;
	font-size: 18px;
	font-weight: 500;
}

.facebook-link {
	font-family: 'GothamRnd-Light', Helvetica, Arial, sans-serif;
	color: @brand-success;
	font-weight: 600;
	font-size: 12px;
}

.linkedin {
	background: url('../img/icons/linkedin.png') no-repeat;
}
.facebook {
	background: url('../img/icons/facebook.png') no-repeat;
}

.signup-social-buttons {
	background-size: 80px;
	padding: 0px;
	border-width: 0px;
	width: 80px;
	height: 80px;
	opacity: 0.9;
	background-color: transparent!important;
	box-shadow: none!important;
	&:hover, &:active, &:focus {
		opacity:1;
		background-color: transparent!important;
		box-shadow: none!important;
	}
}

#email-form-group-w-button {
	button {
		position: absolute;
		right: 0;
		top: -10px;
		background-color: transparent;
		border: 2px solid @brand-success;
		color: @brand-success;
		padding: 10px 14px;
		em {
			font-size: 18px;
		}
		&:hover, &:active, &:focus, &:disabled {
			color: #f3f8f9;
		}
		&:hover {
			background-color: @brand-success;
			border: 2px solid @brand-success;
		}
		&:active, &:focus {
			background-color: @brand-success-dark;
			border: 2px solid @brand-success-dark;
		}
	}
	&.input--normal #password-form-label:after {
		max-width: 100%;
	}
	&.input--empty button {
		top: -20px;
	}
}

#password-form-label {
	left: 0;
	right: 0;
}



button.invalid-disabled {
	background-color: fade(@brand-success, 65%) !important;
	border-color: fade(@brand-success, 65%) !important;
}