
@btn-font-small: 14px;
@btn-font-large: 20px;

.pycbtn-variant(@background; @border; @text-color; @font-size;) {
  font-size: @font-size;
  font-weight: bolder;
  background-color: @background;
  border: none!important;
  vertical-align: middle;
  color: @text-color;
  border-radius: 0;

  &.button-triangle:before {
    border-bottom: 15px solid @background;
    border-left: 12.5px solid transparent;
    border-right: 12.5px solid transparent;
    bottom: 100%;
    content: "";
    position: absolute;
    left: calc(~'50% - 12.5px');
    width: 0;
    height: 0;
  }

  &:hover, &:active, &:focus {
    &.button-triangle:before {
      border-bottom-color: @border;
    }
    background-color: @border;
    color: @text-color;
  }
}

.pycbtn-default {
  .pycbtn-variant(@brand-primary, @brand-primary-dark, @brand-white, @btn-font-large);
}
.pycbtn-success {
  .pycbtn-variant(@brand-success, @brand-success-dark, @brand-white, @btn-font-large);
}
