// Default Variables
@simple-line-font-path        : "../fonts/";
@simple-line-font-family      : "simple-line-icons";
@simple-line-icon-prefix      : simple-icon-;

//Fonts
@font-face {
	font-family: '@{simple-line-font-family}';
	src:  url('@{simple-line-font-path}Simple-Line-Icons.eot?v=2.3.2');
	src:  url('@{simple-line-font-path}Simple-Line-Icons.eot?v=2.3.2#iefix') format('embedded-opentype'),
	url('@{simple-line-font-path}Simple-Line-Icons.woff2?v=2.3.2') format('woff2'),
	url('@{simple-line-font-path}Simple-Line-Icons.ttf?v=2.3.2') format('truetype'),
	url('@{simple-line-font-path}Simple-Line-Icons.woff?v=2.3.2') format('woff'),
	url('@{simple-line-font-path}Simple-Line-Icons.svg?v=2.3.2#simple-line-icons') format('svg');
	font-weight: normal;
	font-style: normal;
}

