/**
   Component: form-validation.less
*/
 
//
// Styles for validation results 
//

.ng-submitted .ng-invalid {
    border-color: @brand-danger;
}
 
.form-validate {
  .form-control {
    

    // Invalid input
    &.ng-invalid.ng-touched, .ng-dirty.ng-invalid {
        border-color: @brand-danger;
    }
    // Valid input
    &.ng-dirty.ng-valid,
    &.ng-dirty.ng-valid:focus {
        border-color: @brand-success;
    }
  }
}