.settings-container {
  padding: 10px;
  position: relative;
  height: 100%;
  min-height: 520px;
  overflow-x: hidden;
  .panel {
    margin: 10px 0;
    padding-right: 0;
    padding-left: 0;

    .panel-body {
      padding: 0;

      ul {
          padding: 0;
          margin-bottom: 0;
        li {
          padding: 10px;
          border-bottom: 1px solid @brand-info-light;

          h4 {
            color: @brand-success;
          }

          p {
            margin: 5px 0 5px 15px;
            color: @text-muted;
            font-style: italic;
          }

        }

        li:last-child {
          border: none;
          margin-bottom: 0;
        }

        li:hover, li:active, li:focus {
          background-color: @brand-success;

          h4, p, em {
            color: @brand-white;

          }
        }

      }
    }
  }
}

.notification-settings {
  notification-switch-group, .notification-switch-group {
    padding: 10px 0;

    label {
      padding: 5px;
    }

    .switch span {
      background-color: transparent;
      border-color: transparent;
    }
  }
}


.beneficiary-list-item {
  padding: 10px!important;
  height: 50px;
  line-height: 40px;
  p {
    max-width: 50%;
    line-height: 40px;
    margin: 0;
    float: left;
  }
  .btn {
    float: right;
    width: 40px;
    height: 40px;
    padding: 2px;
    text-align: center;
    border-radius: 50%;
    em {
      font-size: 45px;
      line-height: 45px;
      color: grey;
    }

    &:hover {
      box-shadow: none;
      background-color: transparent;
      &.btn-heart em {
        color: @brand-danger;
        text-shadow: 1px 1px 1px rgba(99,34,23,0.50);
      }
      &.btn-eye em {
        color: @brand-primary;
        text-shadow: 1px 1px 1px rgba(52,81,108,0.50);
      }
      &.btn-gift em {
        color: @brand-warning;
        text-shadow: 1px 1px 1px rgba(123,87,31,0.50);
      }
    }

    &.btn-heart-active  {
      background-color: @brand-danger!important;
      em{
        font-size: 30px;
        line-height: 40px;
        color: white;
        background-color: transparent;
      }

    }

  }
}

@media @upto-tablet {
  .beneficiary-list-item {
    height: auto;
    line-height: normal;
    p {
      width: 50%;
      white-space: pre-wrap;
      margin-bottom: 10px;
      line-height: normal;
    }

    .btn {
      width: 30px;
      height: 30px;
      padding: 2px;
      line-height: 30px;
      em {
        font-size: 25px;
        line-height: 30px;
      }

      &.btn-heart-active  {
        em{
          font-size: 25px;
          line-height: 30px;
        }

      }
    }

  }
}

.settings-social-container {

  li {
    .social-image{
      padding: 5px 10px 0 10px;
      &.inactive {
        -webkit-filter: grayscale(100%);
        filter: grayscale(100%);
        opacity: 0.7;
      }
    }
  }
  li:hover, li:active, li:focus {
    background-color: inherit!important;

    h4, p, em {
      color: inherit!important;
    }


  }


}