/**
   Component: messages.less
*/

.back-button {
	padding: 5px;
	padding-top: 10px;
	border-top: 2px solid @brand-primary;
	border-bottom: 2px solid @brand-primary;
	background-color: #fff;
}

.message-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 60px;
}

.chat-container {

}

.chat {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-right: 6px;

  li {
    margin-bottom: 10px;
    padding-bottom: 5px;
    .chat-body {
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
      background-color: #fff;
      border: 1px solid @gray-light;      
      p {
        margin: 0;
        color: @text-muted;
      }
    }
    .chat-header {
      padding: 10px;
    }
    .chat-msg {
      padding: 10px;
      border-radius: @border-radius-small;
    }

    &.left .chat-body {
        margin-left: 60px;
        .chat-msg {
        }
    }

    &.right .chat-body {
        margin-right: 60px;
        .chat-msg {
        }
    }

  }
}

.chat .glyphicon {
    margin-right: 5px;
}

.chat-form-container {
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
  position: relative;
  min-height: 60px;
  left: 0px;
  right: 0;
  bottom: 0px;
  // &.sidebar-open {
  //   left: @aside-wd;
  // }
  &.sidebar-lg-open {
    left: @aside-wd + 280px;
  }
}
@media all and (max-width: 767px){
  .chat-form-container.sidebar-open {
    left:0;
  }
}


.textarea-container {
  position: relative;


  textarea, .textarea-size {
    min-height: 40px;
    font-family: sans-serif;
    font-size: 14px;
    box-sizing: border-box;
    padding: 4px;
    overflow: hidden;
    width: 100%;
    resize: vertical;
  }

  textarea {
    height: 100%;
    position: absolute;
    resize:none;
    white-space: pre-wrap;
  }
}

.textarea-size {
  visibility: hidden;
  white-space: pre-wrap;
  word-wrap: break-word;
  word-break: break-all;
  overflow-wrap: break-word;
}
.send {
  vertical-align: top;
}

// Boxed layout
// -----------------------------
.layout-boxed {
  .app-container {
    > section {
      max-width: @boxed-max-width;
      margin: 0 auto;
    }
    > header {
      width: 100%;
    }
  }

  .container-fh {
    max-width: @boxed-max-width;
    margin: 0 auto;
  }
  .hidden-boxed {
    display: none;
  }
  .fw-boxed {
    width: 100%;
  }
}


// Hidden Footer
// -----------------------------------

.footer-hidden {
  .container-fh {
    bottom: 0;
  }
  .app {
    padding-bottom: 0;
  }
  .app-container > footer {
    display: none;
  }
}


// Full Height Layout
// -----------------------------------
.app-fh {
  position: absolute;
  width: auto;
  overflow: auto;
  top: @header-hg;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 0;
  .transition(left @layout-transition-time ease);
  will-change: left;
  -webkit-overflow-scrolling: touch;

  @media @tablet {
    left: @aside-wd;
  }
  .aside-offscreen &,
  .layout-boxed & {
    left: 0;
  }
}


.lg-message-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-bottom: 0;
  ul {
    &.user-list {
      li {
        &.active, &:hover {
          background-color: @brand-success;
          * {
            color: @brand-white;
          }
        }

        &.user-list-item {
          padding: 10px 5px;
        }
      }
    }
  }
}
