// -------------------------------------------------- */
/* Text with line before and after
// -------------------------------------------------- */

.inline-text {
  line-height: 0.5;
  text-align: center;
  color: @brand-success!important;
  width: 100%;
  span {
    display: inline-block;
    position: relative;
    font-size: 11px;
    opacity: 0.8;
    &:before, &:after {
      content: "";
      position: absolute;
      height: 3px;
      border-bottom: 1px solid @brand-success-light;
      top: 0;
      width: 110px;
    }
    &:before {
      right: 100%;
      margin-right: 15px;
    }
    &:after {
      left: 100%;
      margin-left: 15px;
    }
  }
}


.lg-back-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  text-align: center;
  padding-top: 2px;
  color: white;
  z-index: 10;
  em {
    &.fa-2x {
      font-size: 2em!important;
    }
    &.fa-3x {
      font-size: 3em!important;
    }
  }
  &:hover, &:active, &:focus {
    color: @brand-success;
  }
}


