.lh-35 {
  line-height: 35px;
}


.register.panel {
  background-color: @brand-white;
  border-radius: 0;
  .panel-heading {
    border: none;
    border-radius: 0;
  }
  .panel-body {
    padding: 25px;
  }
}

.register .group-container {
  height: 100%;
  margin-bottom: -50px;
  .input-section {
    height: 15%;
  }
  .results-section {
    height: 85%;
  }

  .results-table {
    border: none;
    td {
      border-top-color: @brand-info-light;
      min-height: 85px;
      padding: 15px;
      white-space: normal;
      p {
        margin-bottom: 0;
      }
      button {
        border-color: @brand-success;
        background-color: transparent;
        padding: 2px 0;
        margin-top: 7px;
        em {
          color: @brand-success;
        }

        &:hover, :focus, :active {
          border-color: transparent;
          background-color: @brand-success;

          em {
            color: @brand-white;
          }
        }
      }
    }
  }
}

.profile {
  .full-container {
    height: 100%;
    margin-bottom: -90px;
  }
  img {
    border: 2px solid @brand-success-light;
  }
}

@ui-fg-color: @brand-warning;
@ui-bg-color: #ddd;
@ui-input-wd: 25px;

.register {
  .c-checkbox,
  .c-radio {
    margin-right: 4px;
    * {
      cursor: pointer;
    }
    input {
      opacity: 0;
      position: absolute;
      margin-left: 0 !important;
    }

    label {
      font-size: 15px;
      line-height: 30px;
      font-weight: 600;
    }

    // the element that replaces the input
    span {
      position: relative;
      display: inline-block;
      vertical-align: top;
      margin-left: -@ui-input-wd;
      width: 25px; height: 25px;
      border-radius: 2px;
      font-size: 12px;
      border: 1px solid #ccc;
      margin-right: (@ui-input-wd/2);
      text-align: center;
      &:before {
        margin-left: 1px; // fa align trick
        color: #fff;
      }
    }

    &:hover span {
      border-color: @ui-fg-color;
    }

    .form-inline & span {
      margin-left: 0;
    }

    &.c-checkbox-rounded,
    &.c-radio-rounded {
      span {
        border-radius: 500px;
      }
    }
  }
}


.register-profile-container {
  top: 0!important;

  .pycbtn-success-sm {
    margin: 0 3px;
    width: 47%;
  }

  .checkbox.c-checkbox {
    span {
      width: 25px;
      height: 25px;
    }
    small {
      font-size: 11px;
    }
  }

  .register-terms {
    font-size: 11px;
  }
}

