nav.nav-slit a {
  position: absolute;
  top: 50%;
  margin-top: -80px;
  display: block;
  outline: none;
  text-align: left;
  z-index: 1000;
}

nav.nav-slit a.prev {
  left:0;

  &:hover {
    svg.icon {
      fill: rgba(48, 48, 48, 1);
    }
  }
}

nav.nav-slit a.next {
  right: 0;

  &:hover {
    svg.icon {
      fill: rgba(48, 48, 48, 1);
    }
  }
}

nav.nav-slit a svg {
  display: block;
  margin: 0 auto;
  padding: 0;
}

/*--------------------*/
/* Slit (inspired by Jam3) */
/*--------------------*/
.color-5 { background: transparent; }

.nav-slit .icon-wrap {
  position: relative;
  display: block;
  padding: 45px 10px;
  background-color: transparent;
  .next, .prev {
    width: 35px;
    height: 70px;
    background-size: 35px 70px;
  }

  .prev {
    background-image: url("../img/icons/prev_arrow_gray.png");
  }
  .next {
    background-image: url("../img/icons/next_arrow_gray.png");
  }

  .next:hover, .prev:hover
  {
    opacity: 0.65;
  }

  @media @upto-desktop {
    padding: 0;
    margin-bottom: 45px;

    .next, .prev {
    padding: 10px;
    background: rgba(0, 0, 0, 0.65) no-repeat center;
    width: 30px;
    height: 75px;
    background-size: 25px 55px;
  }
    .prev {
      background-image: url("../img/icons/prev_arrow_white.png");
    }
    .next {
      background-image: url("../img/icons/next_arrow_white.png");
    }

  }
}

.nav-slit svg.icon {
  -webkit-transition: -webkit-transform 0.3s 0.3s;
  transition: transform 0.3s 0.3s;
  fill: rgba(48, 48, 48, 0.71);
}




/* SVG wrap */
.svg-wrap {
  position: absolute;
  width: 0px;
  height: 0px;
  overflow: hidden;
}
#ad-card {
  position: absolute;
  top: 0;
  right: 0%;
  left: 0%;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  padding: 5%;
  .col-md-12 {
    display: none;
  }

  .lg-ad {
    @media (min-width: @screen-lg-min) {
      display: flex;
      overflow: hidden;
      height: inherit;
      z-index:150;
      cursor: pointer!important;
    }
  }

  .md-ad {
    @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
      display: flex;
      overflow: hidden;
      height: inherit;
      z-index:150;
      cursor: pointer!important;
    }
  }
  .sm-ad {
    @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
      display: flex;
      overflow: hidden;
      height: inherit;
      z-index:150;
      cursor: pointer!important;
    }
  }
  .xs-ad {
    @media (max-width: @screen-xs-max) {
      display: flex;
      overflow: hidden;
      height: inherit;
      z-index:150;
      cursor: pointer!important;
    }
  }



}
#cardblur {
  position: absolute;
  top: 0;
  right: 0%;
  left: 0%;
  bottom: 0;
  width: 100%;
  background-image: url("../img/card_bg_blur.jpg");
  .background-size(cover);
  height: 100%;

  .card-img-vertical {
      width: auto;
      height: 100%;
      margin: 0 auto!important;
      display: block;
  }

  .card-img-horizontal {
    height: auto;

    width: 100%;
    margin: 0 auto!important;
    display: block;
  }

}




@media @desktop-desktopLg {
  nav.nav-slit {
    position: absolute;
    top: 15px;
    left: 15px;
    right: 15px;
    bottom:  5%;
    overflow: hidden;
    margin: 0;
  }
  cardview {
    top: 15px;
    left: 15px;
    right: 15px;
    bottom:  5%;
    margin: 0px;
  }

  .card-footer {
    height: 70px;
    width: auto;
    padding: 0px;
    position: absolute;
    left: 15px;
    right: 15px;
    bottom: 0;
    z-index: 5;
    a {
      height: auto;
      button {
        em {
          font-size: 18px;
          margin-right: 5px;
        }
      }
    }
  }

  #cardblur {
    .panel-heading {
      img {
        width: 100%;
        height: 100%;
        margin: 0!important;
      }
      .col-md-7, .col-md-5, .col-md-12 {
        height: auto;
        color: @brand-success!important;
      }
    }
  }

}
@media @desktop-lg {
