//
// Carousel
// --------------------------------------------------

.metrics-container {
  position: absolute;
  left: 5px;
  right: 5px;
  bottom: 5px;
  top: 0;
  padding: 0!important;
}

.metrics-section-title {
  position: absolute;
  left: 0;
  color: @brand-inverse-dark;
  font-family: "GothamRnd-Light", sans-serif;
  text-transform: uppercase;
  right: 0;
  top: 0;
  max-height: 7%;
}
// Wrapper for the slide container and indicators
.carousel {
  //position: absolute;
  //top: 7%;
  //bottom: 0;
  //left: 0;
  //right: 0;
}

.carousel-inner {
  position: relative;
  overflow: hidden;
  width: 100%;


  .panel {
    max-height: 100%;
    box-shadow: none;
    background: transparent!important;
  }
}
.ol-attribution {
  display: none;
}
// Optional indicator pips
//
// Add an unordered list with the following class and add a list item for each
// slide your carousel holds.
.carousel-control.left, .carousel-control.right {
  top: 20%;
  bottom: 20%;
}
.carousel-indicators {
  //position: absolute;
  //top: 10px;
  //left: 5%;
  //right: 5%;
  //z-index: 15;
  //width: 90%;
  //margin-left: 0;
  //padding-left: 0;
  //list-style: none;
  //text-align: center;
  //bottom: auto!important;
  //border-bottom: 1px solid @brand-info-light;
  display: none;
  li {
    display: inline-block;
    width:  10px;
    height: 10px;
    margin: 2px 8px;
    text-indent: -999px;
    border: 1px solid @carousel-indicator-border-color;
    border-radius: 10px;
    cursor: pointer;

    // IE8-9 hack for event handling
    //
    // Internet Explorer 8-9 does not support clicks on elements without a set
    // `background-color`. We cannot use `filter` since that's not viewed as a
    // background color by the browser. Thus, a hack is needed.
    // See https://developer.mozilla.org/en-US/docs/Web/Events/click#Internet_Explorer
    //
    // For IE8, we set solid black as it doesn't support `rgba()`. For IE9, we
    // set alpha transparency for the best results possible.
    background-color: #000 \9; // IE8
    background-color: rgba(0,0,0,0); // IE9
  }
  .active {
    width:  10px;
    height: 10px;
    margin: 2px 8px;
    background-color: @carousel-indicator-active-bg;
  }
}

@lg-circle-size: 280px;
@sm-circle-size: @lg-circle-size / 1.8;

.circle-container {
  position: relative;
}

.y-scroll {
  overflow-y: scroll;
}

.circle-content {
  position: relative;
  display: inline-block;
  text-align: right;
  .number {
    font-weight: 900;
    margin: 0;
    color: white;
  }
  .metric-title {
    text-transform: uppercase;
    margin: 0;
    font-size: 12px;
    color: rgba(248, 248, 255, 0.90);
  }
  .metric-text {
    margin: 0;
    float: right;
    text-align: right;
    max-width: 70%;
    font-size: 9px;
    color: rgba(248, 248, 255, 0.70);
  }
}

.large-circle {
  position: relative;
  display: inline-block;
  width: @lg-circle-size;
  height: @lg-circle-size;
  padding: 25% 0;
  border-radius: 50%;
  /* Just making it pretty */
  @shadow: rgba(0, 0, 0, .1);
  @shadow-length: 4px;
  -webkit-box-shadow: 0 @shadow-length 0 0 @shadow;
  box-shadow: 0 @shadow-length 0 0 @shadow;
  text-align: center;

  .circle-content {
    width: 45%;


  }
}

.small-circle {
  position: absolute;
  display: block;
  width: @sm-circle-size;
  height: @sm-circle-size;
  bottom: -(@sm-circle-size/3);
  left: 10%;
  border-radius: 50%;
  padding: 9% 0 0 0;
  border: none;
  .shadow-z4;
  text-align: center;
  .circle-content {
    width: 100%;

    .metric-text {
      float: none;
      text-align: center;
      display: block;
      margin: 0 auto;
    }
  }
}


.easypie {
  display: inline-block;
  position: relative;

  span {
    display: block;
    position: absolute;
    left: 50%;
    top: 34%;
    width: 100%;
    margin-left: -50%;
    height: 30px;
    margin-top: -15px;

    h1 {
      font-size: 75px!important;
      font-weight: 300;
      text-shadow: 2px 4px 3px rgba(0,0,0,0.3);
    }
  }
}

.metricdetail {
  letter-spacing: 1px;
}

.metric-icon {
  display: block;
  margin: 0 auto;
}


.metric-arrow-left {
  content: "";
  width: 1em;
  height: 1em;
  border-left: 0.1em solid @brand-success;
  border-top: 0.1em solid @brand-success;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  margin-right: 0.5em;
  display: inline-block;
  top: 50%;
  position: absolute;
  left: 20%;
  bottom: 0px;
}

.metric-arrow-right {
  content: "";
  width: 1em;
  height: 1em;
  border-right: 0.1em solid @brand-success;
  border-top: 0.1em solid @brand-success;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  margin-right: 0.5em;
  display: inline-block;
  top: 50%;
  position: absolute;
  left: 20%;
  bottom: 0px;
}


.flip {
  .perspective(800);
  height: 600px;
  width: 80%;
  text-align: center;
  position: relative;
  margin: 50px 10%;
}
.flip .mcard.flipped {
  .rotateX(-180deg);
}
.flip .mcard {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  .transition( 0.8s);
  .transition-timing-function(ease-in-out);
}
.flip .mcard .face {
  width: 100%;
  height: 100%;
  position: absolute;
  .backface-visibility(hidden);
  z-index: 2;
  .panel {
    height: 100%!important;
  }
}
.flip .mcard .front {
  position: absolute;
  z-index: 1;
  cursor: pointer;
}
.flip .mcard .back {
  .rotateX(-180deg);
  color: black;
  cursor: pointer;
}

