/**
   Component: mailbox.less
*/



@mail-box-active-item:          #5cacde;
@mail-box-hover-item:           #e9eeef;
@mail-mails-active:             #f7f8f9;

@mail-nav-item-hover:           @gray-lighter;
@mb-folder-box-hg:              100px;

.mb-folder-box {
  position: relative;
  z-index: 200;
  padding: 20px 0 !important;
  background-color: @gray-darker;
  height: @mb-folder-box-hg;
  @media @desktop-lg {
    height: 100%;
  }
}

.mb-boxes {
  height: 0;
  margin: 0;
  background: inherit;

  @media @desktop-lg {
    height: auto !important;
    display: block;
    visibility: visible;
  }
}

.mb-folder-list {
  list-style: none;
  padding: 0;
  margin: 0;
  > li {
    display: block;
    > a {
      display: block;
      padding: 12px 20px;
      color: lighten(@gray-darker, 50%);
      &.active, &:hover {
        background-color: lighten(@gray-darker, 5%);
        color: #fff;
      }
    }
  } 
}

.mb-buttons {
  .clearfix;
  .mb-btn-compose,
  .mb-btn-folder {
    padding: 15px;
  }
  .mb-btn-compose {
    float: left;
  }
  .mb-btn-folder {
    float: right; 
  }
  @media @desktop-lg {
    .mb-btn-compose {
      float: none;
    }
    .mb-btn-folder {
      display: none;
    }
  }
  > h3 {
    text-indent: -50px;
  }
}

.mb-mails-responsive {
  max-width: 100%;
  overflow: auto;
}

.mb-mails {

  > tbody > tr > td {
    border-top: 1px solid @gray-light;
    &:first-child {
      padding-left: 20px;
    }
    &:last-child {
      padding-right: 20px;
    }
  }

  .mb-mail-active {
    background-color: @mail-mails-active !important;
  }

  .mb-mail-date {
    display: block;
    float: right;
    color: @text-muted;
    font-size: 12px;
    font-weight: bold;
  }

  .mb-mail-from {
    display: block;
    color: @gray-darker;
    font-size: 17px;
    font-weight: 600;
    line-height: 1.2;
  }

  .mb-mail-subject {
    color: @gray-darker;
    font-size: 13px;
    font-weight: 600;
    line-height: 1.8;
    .text-ellipsis;
    padding: 0;
    max-width: 28ch;
  }

  .mb-mail-preview {
    clear: both;
    margin-top: 10px;
    color: @text-muted;
  }
  .mb-unread {
    background-color: @gray-lighter;
  }
}

.mb-mail-compose {
  padding: 10px 15px;
}
