
.pyclist {

  .listtable {
    > tbody > tr > td {
      border-top: 1px solid @brand-info-light;
      padding: 0;
    }
  }

}

.list-container {
  .scroll-content {
    padding: 0 15px;
  }
}

.list-row {
  margin: 15px 0;
  padding: 0;
  display: table;
  width: 100%;
  position: relative;
  .col-xs-4 {
    height: 85px;
    width: 85px;
  }
  img {
    padding: 0;
    height: 100%;
    width: 100%;
    position: absolute;
  }
}

.list-title {
  font-family: 'GothamRnd-Light',sans-serif;
  color: @brand-success;
}


.list-name {
  margin: 0;
  line-height: 85px;
  position: relative;
  font-family: 'GothamRnd-Light',sans-serif;
  color: @brand-success;
  font-size: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; /* fallback */
}

.message-time {
  font-family: GothamRnd-Light;
  font-size: 12px;
}