/**
   Component: animate.less
*/

@charset "UTF-8";

@animate-fade-distance: 18px;

@animate-default-duration: .5s;
@animate-view-duration: .35s;

// Angular standard animation state
// -----------------------------------
[ui-view],
[data-ui-view] {
  .ng-leave {
    display: none;
    &.ng-fluid {
      display: block;
    }
  }
}

.ng-fluid.ng-animate {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden
}

// Animations adapted for ngAnimate
// -----------------------------------

.ng-fadeInLeft2 {
  &.ng-enter { .animation(fadeInLeft2 @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutRight @animate-view-duration); }
}

.ng-fadeIn {
  &.ng-enter { .animation(fadeIn @animate-view-duration);  }
  &.ng-leave { .animation(fadeOut @animate-view-duration); }
}

.ng-fadeInUp {
  &.ng-enter { .animation(fadeInUp @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutDown @animate-view-duration); }
}

.ng-fadeInDown {
  &.ng-enter { .animation(fadeInDown @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutUp @animate-view-duration); }
}

.ng-fadeInRight {
  &.ng-enter { .animation(fadeInRight @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutLeft @animate-view-duration); }
}

.ng-fadeInLeft {
  &.ng-enter { .animation(fadeInLeft @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutRight @animate-view-duration); }
}

.ng-fadeInUpBig {
  &.ng-enter { .animation(fadeInDownBig @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutDownBig @animate-view-duration); }
}

.ng-fadeInDownBig {
  &.ng-enter { .animation(fadeInUpBig @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutUpBig @animate-view-duration); }
}

.ng-fadeInRightBig {
  &.ng-enter { .animation(fadeInRightBig @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutLeftBig @animate-view-duration); }
}

.ng-fadeInLeftBig {
  &.ng-enter { .animation(fadeInLeftBig @animate-view-duration);  }
  &.ng-leave { .animation(fadeOutRightBig @animate-view-duration); }
}

.ng-fadeOutZoom {
  &.ng-enter { .animation(zoomIn 1s @easeOutQuint);  }
  &.ng-leave { .animation(fadeOut 1s @easeOutQuint); }
}


// Animate.css - http://daneden.me/animate
// animo support
// -----------------------------------

.animated {
  -webkit-animation-duration: @animate-default-duration;
  animation-duration: @animate-default-duration;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.fadeInLeft2 {
  -webkit-animation-name: fadeInLeft2;
  animation-name: fadeInLeft2;
}

@-webkit-keyframes fadeInLeft2 {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-@animate-fade-distance, 0, 0);
    transform: translate3d(-@animate-fade-distance, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeInLeft2 {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-@animate-fade-distance, 0, 0);
    -ms-transform: translate3d(-@animate-fade-distance, 0, 0);
    transform: translate3d(-@animate-fade-distance, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
}

// Timing functions
// -----------------------------------

// Cubic
@easeInCubic:  cubic-bezier(0.550, 0.055, 0.675, 0.190);
@easeOutCubic:  cubic-bezier(0.215, 0.610, 0.355, 1.000);
@easeInOutCubic :  cubic-bezier(0.645, 0.045, 0.355, 1.000);

// Circ
@easeInCirc:  cubic-bezier(0.600, 0.040, 0.980, 0.335);
@easeOutCirc:  cubic-bezier(0.075, 0.820, 0.165, 1.000);
@easeInOutCirc:  cubic-bezier(0.785, 0.135, 0.150, 0.860);

// Expo
@easeInExpo:  cubic-bezier(0.950, 0.050, 0.795, 0.035);
@easeOutExpo:  cubic-bezier(0.190, 1.000, 0.220, 1.000);
@easeInOutExpo:  cubic-bezier(1.000, 0.000, 0.000, 1.000);

// Quad
@easeInQuad:  cubic-bezier(0.550, 0.085, 0.680, 0.530);
@easeOutQuad:  cubic-bezier(0.250, 0.460, 0.450, 0.940);
@easeInOutQuad:  cubic-bezier(0.455, 0.030, 0.515, 0.955);

// Quart
@easeInQuart:  cubic-bezier(0.895, 0.030, 0.685, 0.220);
@easeOutQuart:  cubic-bezier(0.165, 0.840, 0.440, 1.000);
@easeInOutQuart :  cubic-bezier(0.770, 0.000, 0.175, 1.000);

// Quint
@easeInQuint:  cubic-bezier(0.755, 0.050, 0.855, 0.060);
@easeOutQuint:  cubic-bezier(0.230, 1.000, 0.320, 1.000);
@easeInOutQuint :  cubic-bezier(0.860, 0.000, 0.070, 1.000);

// Sine
@easeInSine:  cubic-bezier(0.470, 0.000, 0.745, 0.715);
@easeOutSine:  cubic-bezier(0.390, 0.575, 0.565, 1.000);
@easeInOutSine:  cubic-bezier(0.445, 0.050, 0.550, 0.950);

// Back
@easeInBack:  cubic-bezier(0.600, -0.280, 0.735, 0.045);
@easeOutBack:  cubic-bezier(0.175,  0.885, 0.320, 1.275);
@easeInOutBack:  cubic-bezier(0.680, -0.550, 0.265, 1.550);


// -------------------------------------------------- */
/* Animation timing
// -------------------------------------------------- */
.animated-800 {
  animation-duration: 0.6s!important;
}
.animated-600 {
  animation-duration: 0.8s!important;
}
.animated-1000 {
  animation-duration: 1s!important;
}
.animated-1200 {
  animation-duration: 1.2s!important;
}
.animated-1400 {
  animation-duration: 1.4s!important;
}
.animated-1600 {
  animation-duration: 1.6s!important;
}
.animated-1800 {
  animation-duration: 1.8s!important;
}
.animated-2000 {
  animation-duration: 2s!important;
}

.animated-delay {
  -webkit-animation-delay: 2s;
  -o-animation-delay: 2s;
  animation-delay: 2s;
}

.animated-delay-slow {
  -webkit-animation-delay: 1s;
  -o-animation-delay: 1s;
  animation-delay: 1s;
}

@spinner-size: 8em;
.pyc-spinner {
  margin: 150px auto;
  font-size: 9px;
  position: absolute;
  left: 50%;
  margin-left: -(@spinner-size/2);
  text-indent: -9999em;
  border-top: 1.1em solid rgba(40,93,99, 0.2);
  border-right: 1.1em solid rgba(40,93,99, 0.2);
  border-bottom: 1.1em solid rgba(40,93,99, 0.2);
  border-left: 1.1em solid #285d63;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
.pyc-spinner,
.pyc-spinner:after {
  border-radius: 50%;
  width: @spinner-size;
  height: @spinner-size;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
/*
 *  Usage:
 *
 *    <div class="sk-spinner sk-spinner-double-bounce">
 *      <div class="sk-double-bounce1"></div>
 *      <div class="sk-double-bounce2"></div>
 *    </div>
 *
 */
#contentloader {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: transparent;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 150px;
  opacity:0;  /* make things invisible upon start */
  .animation(fadeIn ease-in 1); /* call our keyframe named fadeIn, use animattion ease-in and repeat it only 1 time */
  .animation-fill-mode(forwards);  /* this makes sure that after animation is done we remain at the last keyframe value (opacity: 1)*/
  .animation-duration(1s);

  .sk-spinner-double-bounce.sk-spinner {
    width: 60px;
    height: 60px;
    position: relative;
    margin: 0 auto; }
  .sk-spinner-double-bounce .sk-double-bounce1, .sk-spinner-double-bounce .sk-double-bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: @brand-success;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-animation: sk-doubleBounce 2s infinite ease-in-out;
    animation: sk-doubleBounce 2s infinite ease-in-out; }
  .sk-spinner-double-bounce .sk-double-bounce2 {
    -webkit-animation-delay: -1s;
    animation-delay: -1s; }

  @-webkit-keyframes sk-doubleBounce {
    0%, 100% {
      .scale(0);
    }
    50% {
      .scale(1);
    }
  }

  @keyframes sk-doubleBounce {
    0%, 100% {
      .scale(0);
    }
    50% {
      .scale(1);
    }
  }
}


#imageLoader {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  padding: 35%;
  background-color: @brand-success;
  .spinner {
    width: 25px;
    height: 25px;
    position: static;
    margin: auto;
    background-color: @brand-white;
    border-radius: 100%;
    .animation(scaleout 1.0s infinite ease-in-out);
  }

  @-webkit-keyframes scaleout {
    0% { -webkit-transform: scale(0.0) }
    100% {
      -webkit-transform: scale(1.0);
      opacity: 0;
    }
  }

  @keyframes scaleout {
    0% {
      .scale(0.0);
    } 100% {
        .scale(1.0);
        opacity: 0;
      }
  }
}


